
import { defineComponent } from "vue";
import MaterialInput from "./MaterialInput.vue";

export default defineComponent({
  name: "RegisterModal",
  components: {
    MaterialInput,
  },
  data: function() {
    return {
      name: "",
      email: "",
      password: "",
      showModal: false,
    };
  },
  computed: {
    isAuthenticated(): boolean {
      return this.$store.direct.state.AuthMod.isAuthenticated;
    },
    isDarkMode(): boolean {
      return this.$store.direct.state.DarkModeMod.isDarkMode;
    },
  },
  methods: {
    close() {
      this.showModal = false;
      this.$emit("closeModal", false);
    },
    handleRegister() {
      this.$store.direct.dispatch.register({
        name: this.name,
        email: this.email,
        password: this.password,
      });
    },
    handleKeyDown(e: KeyboardEvent) {
      if (e.code === "Enter") {
        this.handleRegister();
      }
    },
    handleNameInput(value: string) {
      this.name = value;
    },
    handleEmailInput(value: string) {
      this.email = value;
    },
    handlePasswordInput(value: string) {
      this.password = value;
    },
  },
  watch: {
    isAuthenticated: function() {
      this.$emit("closeModal");
    },
  },
});
