
import { defineComponent } from "vue";

export default defineComponent({
  name: "DropDown",
  data: function() {
    return {
      isOpen: false,
    };
  },
  methods: {
    openLoginModal() {
      this.$store.direct.commit.openLoginModal();
    },
    logOut() {
      this.$store.direct.dispatch.logout();
    },
    handleClick() {
      this.isOpen ? (this.isOpen = false) : (this.isOpen = true);
    },
  },
});
